/*#region INPUTS**/
input {
    background-color: #313131;
    display: inline-block;
    color: #FFC0CB;
    font-family: "American Typewriter", serif;
    font-size: larger;
    text-align: center;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}

input::selection {
    background-color: transparent
}

input.dm {
    border-color: #1f1f1f
}

/*#endregion*/

/*#region KEYS*/
.key {
    border-radius: 2px;
}

.key.R0 {
    width: 10%;
    color: #FFC0CB;
    background-color: #313131;
}

.key.R1 {
    width: 11.11%;
}

.key.R2 {
    width: 10%;
}

.key.DL {
    width: 15%;
    background-image: url(/better-wordle/static/media/backspace-icon.2d22e2c0.png);
    background-repeat: no-repeat;
    background-position: center;
    color: transparent;
}

.key.EN {
    width: 15%;
}

/*#endregion*/

/*#region KEYBOARD BOX*/
.key-row {
    box-sizing: border-box;
    height: 33.33%;
    margin: 0 auto;
    border-color: inherit;
}

.key-box {
    height: 100%;
    width: 100%;
    border-color: #D8BFD8
}

.keyContainer {
    width: 100%;
    min-width: 500px;
    max-width: 800px;
    height: 130px;
}

/*#endregion*/

/*#region BUTTONS*/
input.headerButton {
    border-radius: unset;
    margin: unset;
    background-color: unset;
    display: inline-block;
    color: #000000;
    text-transform: unset;
    border-radius: 2px;
    border-width: 2px;
    box-sizing: border-box;
    font-size: 130%;
    font-family: "Times New Roman", serif;
    width: 33.33%;
    min-height: 38px;
    min-width: 132px;
    display: inline-block;
    border-color: #000000;
    background-color: rgb(225, 225, 225)
}

input.headerButton:hover {
    background-color: rgb(203, 168, 202);
    -webkit-transform: scale3d(0.98, 0.96, 1);
            transform: scale3d(0.98, 0.96, 1)
}

input.headerButton.dm {
    background-color: #313131;
    color: #ffcfcf;
    border-color: #000000
}

input.headerButton:hover.dm {
    background-color: #761919;
    color: #ff6982;
    -webkit-transform: scale3d(0.96, 0.96, 1);
            transform: scale3d(0.96, 0.96, 1)
}

/*#endregion*/
div {
    box-sizing: border-box;
    margin: 0 auto;
    padding-top: unset;
}

body {
    margin: 0 auto;
    padding-top: 0.5%;
    align-content: center;
    text-align: center;
    background-color: #D8BFD8
}

p {
    border: 2px solid black;
    box-sizing: border-box;
    margin: 0;
    font-family: "American Typewriter", serif;
    font-size: 200%;
    display: inline-block
}

p.word {
    border: 2px solid transparent;
    height: 100%;
    color: #D8BFD8;
    text-transform: uppercase;
}

p.word.l1 {
    color: transparent
}

div.appBox {
    -webkit-user-select: none;
    user-select: none;
}

div.container.headerBox {
    min-width: 500px;
    width: 50%;
}

div.container.headerBox.dm {
    background-color: #712b06
}

div.container {
    border: 3px solid rgb(255, 148, 71);
    min-width: -moz-fit-content;
    min-width: -webkit-fit-content;
    min-width: fit-content;
}

div.container.dm {
    border: 3px solid #712b06
}

div.row {
    box-sizing: border-box;
    display: inline-block;
}

.container.wordContainer {
    height: 100%;
    width: 250px;
    box-sizing: border-box;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.wordBoxContainer {
    margin: 0 auto;
    width: 500px;
}

div#input.container.wordContainer {
    background-color: #313131;
    color: #FFC0CB;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

div#input.container.wordContainer.dm {
    background-color: transparent;
    color: #FFC0CB;
}

div.gameBox {
    display: inline-flex;
    max-width: 1000px;
    min-width: 500px;
    flex-wrap: wrap;
}

.wordInput {
    text-align: left;
    text-indent: 26px;
    letter-spacing: 56px;
    caret-color: transparent;
    width: 345px;
    height: 49px;
    -webkit-text-fill-color: #FFC0CB;
}

#wordInputBox {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    width: 350px;
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px)
}

.title-box {
    font-family: "Times New Roman", serif;
    font-size: 230%;
    display: block;
    width: 100%;
}

#plain {
    background-color: rgb(225, 225, 225)
}

#error {
    background-color: rgb(168, 14, 14)
}

.dm#plain {
    background-color: #262626;
    color: #FFC0CB
}

.dm#error {
    background-color: #570909;
    color: #000000
}

.letter {
    width: 20%;
    height: 100%;
    min-height: 36.8px;
    text-align: center
}

.letter#boxColor-2 {
    background-color: rgb(225, 225, 225);
    color: transparent
}

.letter.dm#boxColor-2 {
    background-color: #262626;
    color: transparent
}

.letter#boxColor-1 {
    background-color: rgb(225, 225, 225)
}

.letter.dm#boxColor-1 {
    background-color: #262626;
    color: #FFC0CB
}

.letter#boxColor0 {
    background-color: rgb(255, 199, 30)
}

.letter.dm#boxColor0 {
    background-color: #FFA500;
    color: #892d2d
}

.letter#boxColor1 {
    background-color: rgb(99, 190, 93)
}

.letter.dm#boxColor1 {
    background-color: #004500;
    color: #FFFFFF
}

#ans-box {
    width: 100%;
    font-size: 70%;
    background-color: #262626;
    border-color: #000000;
    font-stretch: condensed;
    height: 40.5px;
    text-indent: 0px;
}
